import React, { Component } from "react";
import Clock from "../../components/Clock/Clock";

class Countdown extends Component{
	constructor(props){
		super(props);
		this.state={
			deadline : "January, 1, 2021, 00:00:00"
		};
	}

	render(){
		return(
			<Clock deadline={this.state.deadline}/>
		);
	}
}

export default Countdown;