import React from "react";
import Card from "react-bootstrap/Card";
import "./ClockFace.css";
const ClockFace = ({ data, type }) => {
	return (
		<Card
			bg="danger"
			text="white"
			className="mb-2">
			<Card.Body
			style={{ padding: '0', margin: 'auto', height:'5rem', display: 'flex', flexDirection: 'column', justifyContent:'center'}}>
				<Card.Title
				style={{fontSize: '3rem', margin: '0px'}}
					> {data} </Card.Title>
				<Card.Text> {type} </Card.Text>
			</Card.Body>
		</Card>
	);
}

export default ClockFace;